<template>
  <modal @close="handleClose">
    <section class="financial-calculator">
      <div class="title c-font-20"></div>
      <div class="row2">{{monthlyInstallmentPaymentRough}}</div>
      <section
        v-for="(item, index) in list"
        class="item c-gap-top-20">
        <div
          class="row1 c-flex">
          <div class="left">{{item.label}}</div>
          <div class="right c-flex">
            <el-input
              v-model="form[item.formName]">
            </el-input>
            <span
              class="unit c-gap-left-20">
              {{item.unit}}
            </span>
          </div>
        </div>
        <el-slider v-model="form[item.formName]"></el-slider>
      </section>
    </section>
  </modal>
</template>

<script>
  import Modal from '../../components/Modal/Modal.vue';

  export default {
    name: 'FinancialCalculatorModal',
    data() {
      return {
        monthlyInstallmentPaymentRough: '2000.00', // 
        form: {
          carPrice: 36, // 
          downPayment: 42,  // 
          substitution: 56, // 
          purchaseTax: 12, // 
          luxuryCarPurchaseTax: 24, // 
          interestRate: 98, // 
          loansPeriod: 34, // 
        },
        list: [
          {
            label: '',
            formName: 'carPrice',
            unit: 'RMB'
          },
          {
            label: '',
            formName: 'downPayment',
            unit: '%'
          },
          {
            label: '',
            formName: 'substitution',
            unit: '%'
          },
          {
            label: '',
            formName: 'purchaseTax',
            unit: '%'
          },
          {
            label: '',
            formName: 'luxuryCarPurchaseTax',
            unit: '%'
          },
          {
            label: '',
            formName: 'interestRate',
            unit: '%'
          },
          {
            label: '',
            formName: 'loansPeriod',
            unit: ''
          }
        ]
      }
    },
    components: {
      Modal
    },
    methods: {
      handleClose() {
        this.$emit('close');
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/app";

  .financial-calculator {
    padding: 0 20px;
    width: $modalWidth;
    height: $modalHeight;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: center;
    box-sizing: border-box;
  }

  .item {

    .row1 {
      justify-content: space-between;

      .unit {
        width: 40px;
      }
    }
  }
</style>



