<template>
  <section class="logistics-order c-gap-inner-header c-container">
    <!---->
    <div class="c-gap-top-20 c-font-20 c-bold"></div>
    <section class="c-gap-top-20">
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        list-type="picture-card"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove">
        <i class="el-icon-plus"></i>
      </el-upload>
    </section>
    <!---->
    <section class="c-flex row2">
      <!---->
      <section class="row-item">
        <div class="c-font-20 c-bold"></div>
        <section class="c-gap-top">
          <div
            v-for="(item, index) in data.saveTemplates"
            :key="index">
            {{item.text}}
          </div>
        </section>
      </section>
      <!---->
      <section class="row-item">
        <div class="c-font-20 c-bold"></div>
        <section class="c-gap-top">
          <div
            v-for="(item, index) in data.receivePrices"
            :key="index">
            {{item.text}}
          </div>
        </section>
      </section>
      <!---->
      <section class="row-item-full-width">
        <div class="c-font-20 c-bold"></div>
        <section class="c-gap-top">
          <div
            v-for="(item, index) in data.ignorePrices"
            :key="index">
            {{item.text}}
          </div>
        </section>
      </section>
      <!---->
      <section class="row-item-full-width">
        <div class="c-font-20 c-bold"></div>
        <section class="c-gap-top">
          <div
            v-for="(item, index) in data.processingOrders"
            :key="index">
            {{item.text}}
          </div>
        </section>
      </section>
    </section>
    <!---->
    <section class="divider c-divider"></section>
    <!---->
    <section class="car-info">
      <div class="c-font-20 c-bold"></div>
      <div class="c-gap-top-20 c-flex">
        <div class="c-gap-right-20">{{data.carInfo.brand}}</div>
        <div class="c-gap-right-20">{{data.carInfo.year}}</div>
        <div class="c-gap-right-20">{{data.carInfo.price}}</div>
        <div class="c-gap-right-20">{{data.carInfo.body}}</div>
        <div class="c-gap-right-20">{{data.carInfo.color}}</div>
      </div>
      <div class="c-gap-top c-flex">
        <div class="c-gap-right-20"></div>
        <div class="c-gap-right-20"></div>
      </div>
      <div class="c-gap-top">____  / </div>
      <div class="c-gap-top">____</div>
    </section>
    <!---->
    <section class="contact">
      <div class="c-font-20 c-bold"></div>
      <div class="c-gap-top-20 c-flex">
        <div class="c-gap-right-20">____  / </div>
        <div class="c-gap-right-20">_______________</div>
      </div>
    </section>
    <!---->
    <section class="carAddress c-gap-top-40">
      <div class="c-font-20 c-bold"></div>
      <div class="c-gap-top-20 c-flex">
        <el-cascader
          class="c-gap-right-20"
          :options="data.carAddress.options"
          v-model="data.carAddress.value"
          :placeholder="data.carAddress.placeholder">
        </el-cascader>
        <div class="c-gap-right-20">_______________</div>
      </div>
    </section>
    <!---->
    <section class="destination c-gap-top-40">
      <div class="c-font-20 c-bold"></div>
      <div class="c-gap-top-20 c-flex">
        <el-cascader
          class="c-gap-right-20"
          :options="data.destination.options"
          v-model="data.destination.value"
          :placeholder="data.destination.placeholder">
        </el-cascader>
        <div class="c-gap-right-20">_______________</div>
      </div>
    </section>
    <!---->
    <section class="c-flex c-gap-top-40 btn-layout">
      <el-button type="success"></el-button>
    </section>
  </section>
</template>

<script>
  import options from '../../components/Cascader/cascader-address-options';

  export default {
    name: 'LogisticsOrder',
    data() {
      return {
        data: {
          // 
          saveTemplates: [
            {
              text: '2016  CLA '
            },
            {
              text: '2016  X5 '
            },
          ],
          // 
          receivePrices: [
            {
              text: ' CLA  3000',
            },
            {
              text: ' CLA  3500',
            },
          ],
          // 
          ignorePrices: [
            {
              text: 'Carry  CLA  5000',
            },
          ],
          // 
          processingOrders: [
            {
              text: '2016  CLA  5000 ---------------- ',
            },
          ],
          // 
          carInfo: {
            brand: '',  // 
            year: '', // 
            price: '',  // 
            body: '', // 
            color: '',  // 
          },
          // 
          destination: {
            options: options,
            value: [],
            placeholder: ''
          },
          // 
          carAddress: {
            options: options,
            value: [],
            placeholder: ''
          }
        }
      }
    },
    components: {},
    methods: {
      handlePictureCardPreview() {
        console.log('handlePictureCardPreview');
      },
      handleRemove() {
        console.log('handleRemove');
      }
    },
    mounted() {
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../assets/app";

  .logistics-order {

    .row2 {
      flex-wrap: wrap;
      align-items: start;

      .row-item, .row-item-full-width {
        margin-top: 40px;
      }

      .row-item {
        width: 50%;
      }

      .row-item-full-width {
        width: 100%;
      }
    }

    .divider {
      margin-top: 40px;
      margin-bottom: 40px;
      height: 1px;
    }

    .contact {
      margin-top: 40px;
    }

    .btn-layout {
      justify-content: center;
    }
  }

</style>



